import Dmsstep from "@/components/Dmsstep.vue";
import Dmsconfirm from "@/components/Dmsconfirm.vue";
import { getStuById } from "@/api/user";
import { getroomstuinfo } from "@/api/dorm";
import { onMounted, reactive, ref, watch } from "vue";
import { useStore } from "vuex";
export default {
  name: "studorminfo",
  components: {
    Dmsconfirm,
    Dmsstep
  },
  setup() {
    const store = useStore();
    const stuaddress = ref("xx栋 x层 xxx房");
    const studorminfolist = reactive({
      list: [{
        bed: "",
        sex: "",
        major: "",
        stuclass: "",
        stunumber: "",
        stuname: "",
        phone: "",
        counsellor: "",
        email: "",
        job: ""
      }]
    });
    const jobtype = ref(0);
    const setroom = () => {
      getStuById({
        stuid: store.state.dsuser.dsuserInfo.id
      }).then(res => {
        if (res.status == 0) {
          store.commit("dsuser/setStuJob", res.stuobj[0].job);
          stuaddress.value = `${res.stuobj[0].building}栋 ${res.stuobj[0].floor}层 ${res.stuobj[0].room}房`;
          // 根据楼栋获取宿舍学生信息
          getroomstuinfo({
            building: res.stuobj[0].building,
            room: res.stuobj[0].room
          }).then(res1 => {
            if (res1.status == 0) {
              jobtype.value = store.state.dsuser.dsuserInfo.job;
              res1.roomstuinfo.forEach(item => {
                if (item.job == 0) {
                  item.job = "舍员";
                } else if (item.job == 1) {
                  item.job = "舍长";
                } else if (item.job == 2) {
                  item.job = "层长";
                } else if (item.job == 3) {
                  item.job = "舍长兼层长";
                }
                if (item.sex == 0) {
                  item.sex = "男";
                } else if (item.sex == 1) {
                  item.sex = "女";
                }
              });
              studorminfolist.list = res1.roomstuinfo;
            } else {
              console.log(res1.message);
            }
          }).catch(err1 => {
            console.log(err1.message);
          });
        } else {
          console.log(res.message);
        }
      }).catch(err => {
        console.log(err.message);
      });
    };
    const deleteid = ref(0);
    const deletestr = ref("确认要转让舍长职务给该舍友吗？");
    const deletetype = ref(14);
    const deletejob = ref(0);
    const changejob = (toid, tojob) => {
      // console.log(toid);
      if (tojob == "舍员") {
        tojob = 0;
      } else if (tojob == "舍长") {
        tojob = 1;
      } else if (tojob == "层长") {
        tojob = 2;
      } else if (tojob == "舍长兼层长") {
        tojob = 3;
      }
      deletejob.value = tojob;
      deleteid.value = toid;
      deletestr.value = "确认要转让舍长职务给该舍友吗？";
      deletetype.value = 14;
    };
    onMounted(() => {
      jobtype.value = store.state.dsuser.dsuserInfo.job;
      setroom();
      watch(() => store.state.dsdorm.stuinfostatus, () => {
        setroom();
      });
    });
    return {
      stuaddress,
      studorminfolist,
      jobtype,
      changejob,
      deleteid,
      deletestr,
      deletetype,
      deletejob,
      setroom
    };
  }
};